//ДЛЯ РАСЧЕТА ------------------------------ divisor - делитель (нижнее число), divisible - делимое
// Рассчетная формула (ввод.вес * нач.цена)/нач.вес | (ввод.ккал * нач.гр)/нач.гр 
export function calculateValue({ myDivisible, initialDivisible, divisor }) {
  if (myDivisible && initialDivisible && divisor)
    return ((myDivisible * initialDivisible) / divisor).toFixed(2)
  return ""
}