import React from 'react';
import { makeStyles, withStyles, useTheme } from '@material-ui/core/styles';
import { Box, Button, Divider, Input, InputAdornment, TextField, Typography } from '@material-ui/core';
import useMediaQuery from '@material-ui/core/useMediaQuery';

import { calculateValue } from 'Utils/Calculate'

const useStyles = makeStyles((theme) => ({
  root: {
    height: '100%',
    boxSizing: 'border-box',
    display: 'grid',
    padding: '.5rem',
    gridGap: '.5rem',
    gridTemplateRows: 'max-content 1fr max-content',
  },
  content: {
    overflow: 'scroll',
    boxSizing: 'border-box',
    display: 'grid',
    // до 599px ~ вертикальная ориентация 
    [theme.breakpoints.down('xs')]: {
      gridAutoRows: 'max-content',
    },
    // после 600px ~ горизонтальная ориентация 
    [theme.breakpoints.up('sm')]: {
      gridTemplateColumns: ' 1fr max-content 1fr',
    },
    '& .MuiDivider-root': {
      [theme.breakpoints.down('xs')]: {
        margin: '1rem',
      },
    },
    '& .MuiInputBase-root': {
      maxWidth: '170px',

    }
  },
  priceInfo: {
    display: 'grid',
    gridTemplateRows: 'repeat(2, max-content)',
    gridTemplateColumns: 'repeat(2, max-content)',
    gridGap: ".5rem",
    alignItems: 'center',
    justifyContent: 'center',
  },
  outputInfo: {
    // '& input.MuiInputBase-input.MuiOutlinedInput-input.MuiInputBase-inputMarginDense.MuiOutlinedInput-inputMarginDense': {
    '& .MuiInputBase-root.MuiOutlinedInput-root.MuiInputBase-formControl.MuiInputBase-adornedEnd.MuiOutlinedInput-adornedEnd.MuiInputBase-marginDense.MuiOutlinedInput-marginDense': {
      maxWidth: '90px',
    },
    '& .MuiInputBase-root.MuiInput-root.MuiInput-underline.Mui-disabled.Mui-disabled.MuiInputBase-formControl.MuiInput-formControl.MuiInputBase-adornedStart.MuiInputBase-adornedEnd.MuiInputBase-marginDense.MuiInput-marginDense': {
      color: 'green',
      maxWidth: '150px',
    }
  },
  justifySelfEnd: {
    justifySelf: 'end',
  },
  justifySelfCenter: {
    justifySelf: 'center',
    textAlign: 'center',
  },
}));

export default function Price({ id }) {
  const classes = useStyles();
  const theme = useTheme();
  const isSizeSM = useMediaQuery(theme.breakpoints.up('sm')); // const theme = useTheme(); useTheme from styles

  const [isСomparison, setIsComparison] = React.useState(false);
  const [inputValues, setInputValues] = React.useState({
    price: "",
    weight: "",
  });
  // значения для сравнения
  const [inputComparisonValues, setInputComparisonValues] = React.useState({
    price: "",
    weight: "",
  });

  const [otherWeight, setOtherWeight] = React.useState("");

  const outputValues = React.useMemo(() => {
    const calculate = (weight) => calculateValue({
      myDivisible: weight,
      initialDivisible: inputValues.price,
      divisor: inputValues.weight
    })
    console.log(
      {
        outputPrice: calculate(otherWeight),
        outputPrice100: calculate(100),
      }
    )
    return ({
      outputPrice: calculate(otherWeight),
      outputPrice100: calculate(100),
    })
    // return calculateValue({
    //   myDivisible: otherWeight,
    //   initialDivisible: inputValues.price,
    //   divisor: inputValues.weight,
    // })
  }, [inputValues.price, inputValues.weight, otherWeight]);

  const buttonLabel = React.useMemo(() => isСomparison ? 'Один' : 'Сравнить два', [isСomparison]);

  const isCheap = React.useMemo(() => {
    return false;
  }, []);

  const onChangeInputValue = React.useCallback((e) => {
    const { name, value } = e.target;
    setInputValues((prev) => ({ ...prev, [name]: value }))
  }, []);

  const onChangeOtherWeight = React.useCallback((e) => {
    const { value } = e.target;
    setOtherWeight(value)
  }, []);

  const onChangeInputComparisonValue = React.useCallback((e) => {
    const { name, value } = e.target;
    setInputComparisonValues((prev) => ({ ...prev, [name]: value }))
  }, []);

  return (
    <div className={classes.root}>
      <Typography className={classes.justifySelfCenter} variant="h5">Введите цену и вес продукта</Typography>
      <div className={classes.content}>
        <div className={classes.priceInfo}>
          <Typography>Цена:</Typography>
          <TextField
            type="number"
            size="small"
            name="price"
            placeholder="введите"
            error={!inputValues.price || inputValues.price < 0}
            value={inputValues.price}
            onChange={onChangeInputValue}
            InputProps={{
              endAdornment: <InputAdornment position="end">руб.</InputAdornment>
            }}
          />
          <Typography className={classes.justifySelfEnd}>Вес:</Typography>
          <TextField
            type="number"
            size="small"
            name="weight"
            placeholder="введите"
            error={!inputValues.weight || inputValues.weight < 0}
            value={inputValues.weight}
            onChange={onChangeInputValue}
            InputProps={{
              endAdornment: <InputAdornment position="end">г.</InputAdornment>
            }}
          />
        </div>
        <Divider variant="middle" orientation={isSizeSM ? 'vertical' : 'horizontal'} />
        <div className={`${classes.priceInfo} ${classes.outputInfo}`} >
          <Typography className={classes.justifySelfEnd} component="span">100 г.</Typography>
          <TextField
            disabled
            type="number"
            size="small"
            placeholder="0.0"
            value={outputValues.outputPrice100}
            InputProps={{
              startAdornment: <InputAdornment position="start">за</InputAdornment>,
              endAdornment: <InputAdornment position="end">руб.</InputAdornment>
            }}
          />
          <TextField
            type="number"
            size="small"
            name="otherWeight"
            placeholder="вес"
            variant="outlined"
            error={otherWeight < 0 || otherWeight > 9999}
            value={otherWeight}
            onChange={onChangeOtherWeight}
            InputProps={{
              endAdornment: <InputAdornment position="end">г.</InputAdornment>
            }}
          />
          <TextField
            disabled
            type="number"
            size="small"
            placeholder="0.0"
            value={outputValues.outputPrice}
            InputProps={{
              startAdornment: <InputAdornment position="start">за</InputAdornment>,
              endAdornment: <InputAdornment position="end">руб.</InputAdornment>
            }}
          />

        </div>
      </div>
      <Box justifySelf="end">
        <Button variant="contained" color="primary" onClick={() => setIsComparison(prev => !prev)}>
          {buttonLabel}
        </Button>
      </Box>
    </div >
  );
}
