import React from 'react';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import { Box, withStyles } from '@material-ui/core';

import Price from 'Components/Price/Price';
import Calorie from 'Components/Calorie/Calorie';


const StyledTabs = withStyles({
  indicator: {
    backgroundColor: "#f0f2ff",
    // backgroundColor: "#3f51b5",
  },
  root: {
    '& [tabindex="-1"]': {
      color: "#3f51b5",
      // color: "#5c6fda",
      background: "white",
      fontWeight: 'lighter',
    }
  }
})((props) => <Tabs {...props} />);

const StyledTab = withStyles((theme) => ({
  root: {
    textTransform: "none",
    color: "#f0f2ff",
    background: '#3f51b5',
    // background: '#5c6fda',
    fontWeight: 'bolder',
  }
}))((props) => <Tab disableRipple {...props} />);

const tabs = [
  {
    id: 0,
    label: 'кбжу100',
    Component: () => <Calorie />,
  },
  {
    id: 1,
    label: 'цена100',
    Component: ({ id }) => <Price id={id} />,
  },
]
const prefix = 'app-tab';

export default function MenuTabs() {
  const [tabIndex, setTabIndex] = React.useState(0);

  const handleChange = (_, newValue) => {
    setTabIndex(newValue);
  };

  return (
    <Box
      boxSizing="border-box"
      height="100%"
      display="grid"
      gridTemplateRows="max-content 1fr"
    >
      <Paper square>
        <StyledTabs
          variant="fullWidth"
          value={tabIndex}
          onChange={handleChange}
          aria-label={`${prefix}s`}
        >
          {tabs.map(({ id, label }) => <StyledTab key={id} label={label} id={`${prefix}-${id}`} aria-controls={`${prefix}-tabpanel-${id}`} />)}
        </StyledTabs>
      </Paper>
      <Box overflow="auto" display="grid" p={1}>
        {
          tabs.map(({ id, Component, label, }) =>
            <div
              key={id}
              role="tabpanel"
              hidden={tabIndex !== id}
              id={`${prefix}-tabpanel-${id}`}
              aria-labelledby={`${prefix}-${id}`}
            >
              {tabIndex === id && <Component id={label} />}
            </div>
          )
        }
      </Box>
    </Box>
  )
}
